@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700family=Cookie&display=swap');

*{
  margin: 0;
  padding: 0%;
  box-sizing: border-box;
  font-family: 'Ubuntu', sans-serif;
}

:root {
  --light: #6dd5ed;
  --dark: #2193b0;
}

body{
  color: #fff ;
  background-color: rgb(0, 49, 51);
}

#logo{
  width: 200px;
  height: 200px;
  position: absolute;
  top: 5%;
  left: 5%;
}

#navbar{
  /* border: 1px solid yellow; */
  height: 10vh;
  width: 60%;
  position: absolute;
  top: 15%;
  right: 9%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: larger;
  font-weight: lighter;
}

#navbar ul{
  display: flex;
 width: 55%;
 justify-content: space-between;
}

#navbar ul li{
  list-style: none;
  padding: 2%;
  cursor: pointer;
  border-radius: 5px;
}

#navbar ul li:hover{
  background: linear-gradient(45deg,var(--light) , var(--dark));
}

#navbar ul li a{
  color: #fff;
  text-decoration: none;
}

#navbar button{
  width: 18%;
  height: 50px;
  font-size: 90%;
  position: relative;
  right: 1%;
  cursor: pointer;
  padding: 1%;
  border-radius: 7px;
}

#navbar button:hover{
  background: linear-gradient(45deg, var(--light), var(--dark));
  color: #fff;
}


#HOME{
  width: 100%;
  height: 100vh;
  display: flex;
  align-items:center;
  justify-content: center;
}

#HOME div{
  /* border: 1px solid royalblue; */
  width: 800px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  
}

#HOME div h4{
  font-size: 150%;
  font-weight: lighter;
  font-family: 'Cookie', cursive;
  background-color: #f3ec78;
  background-image: linear-gradient(45deg, var(--light) , var(--dark));
  background-size: 100%;
  background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-text-fill-color: transparent;
}

#HOME div img{
  width: 370px;
  height: 370px;
}
